"use client"

import { useState, useEffect } from "react"
import { motion } from "framer-motion"
import Link from "next/link"
import { DashboardMockup } from "./dashboard-mockup"
import { Navbar } from "./navbar"
import { LogoCloud } from "./logo-cloud"
import { FeatureCardsSection } from "./feature-cards-section"
import { AISection } from "./ai-section"
import { ProductDirectionSection } from "./product-direction-section"
import { WorkflowsSection } from "./workflows-section"
import { DownloadAppSection } from "./download-app-section"
import { CTASection } from "./cta-section"
import { Footer } from "./footer"

export function Hero3DStage() {
  const [yOffset, setYOffset] = useState(0)
  const [isMobile, setIsMobile] = useState(false)

  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth < 768)
    }
    checkMobile()
    window.addEventListener("resize", checkMobile)

    const handleScroll = () => {
      const scrollY = window.scrollY
      const offset = Math.min(scrollY / 300, 1) * -20
      setYOffset(offset)
    }

    window.addEventListener("scroll", handleScroll, { passive: true })
    return () => {
      window.removeEventListener("scroll", handleScroll)
      window.removeEventListener("resize", checkMobile)
    }
  }, [])

  const baseTransform = {
    translateX: 2,
    scale: isMobile ? 0.8 : 1.2,
    rotateX: 47,
    rotateY: 31,
    rotateZ: 324,
  }

  return (
    <>
      <section className="relative min-h-screen overflow-hidden" style={{ backgroundColor: "#09090B" }}>
        <Navbar />

        {/* Subtle glow - emerald for fintech */}
        <div
          className="absolute pointer-events-none"
          style={{
            top: "50%",
            left: "50%",
            transform: "translate(-50%, -30%)",
            width: isMobile ? "600px" : "1200px",
            height: isMobile ? "400px" : "800px",
            background: "radial-gradient(ellipse at center, rgba(16, 185, 129, 0.08) 0%, transparent 70%)",
          }}
        />

        {/* Main content */}
        <div className="relative z-10 pt-24 md:pt-28 flex flex-col">
          {/* Hero text - contained and centered */}
          <div className="w-full flex justify-center px-4 md:px-6 mt-10 md:mt-16">
            <div className="w-full max-w-4xl">
              <motion.h1
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6 }}
                className="text-3xl sm:text-4xl md:text-5xl lg:text-[56px] font-medium text-white leading-[1.1] text-balance"
              >
                Sua carteira digital para{" "}
                <span className="text-emerald-400">PIX</span> e{" "}
                <span className="text-orange-400">Criptomoedas</span>
              </motion.h1>
              <motion.p
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: 0.1 }}
                className="mt-4 md:mt-6 text-sm md:text-lg text-zinc-400"
              >
                Deposite, transfira e gerencie seu dinheiro digital com total transparencia.
                <br className="hidden sm:block" />
                <span className="sm:hidden"> </span>
                Taxas justas: PIX 0,5% e Crypto 1,0%.
              </motion.p>
              <motion.div
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ duration: 0.6, delay: 0.2 }}
                className="mt-6 md:mt-8 flex flex-col sm:flex-row items-start sm:items-center gap-4 sm:gap-6"
              >
                <Link 
                  href="/login"
                  className="w-full sm:w-auto text-center px-6 py-3 bg-emerald-500 text-zinc-900 font-medium rounded-xl hover:bg-emerald-400 transition-colors text-sm"
                >
                  Criar conta gratis
                </Link>
                <Link 
                  href="https://docs.fluxpay.com"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="text-zinc-300 font-medium hover:text-white transition-colors flex items-center gap-2 text-sm"
                >
                  <span className="text-zinc-500">Desenvolvedores:</span> API publica
                  <span aria-hidden="true">→</span>
                </Link>
              </motion.div>
            </div>
          </div>

          {/* 3D Stage - full bleed, hidden on mobile for better performance */}
          <div
            className="relative mt-8 md:mt-16 hidden md:block"
            style={{
              width: "100vw",
              marginLeft: "-50vw",
              marginRight: "-50vw",
              position: "relative",
              left: "50%",
              right: "50%",
              height: "700px",
              marginTop: "-60px",
            }}
          >
            <div
              className="absolute bottom-0 left-0 right-0 h-72 z-10 pointer-events-none"
              style={{
                background: "linear-gradient(to top, #09090B 20%, transparent 100%)",
              }}
            />

            {/* Perspective container */}
            <div
              style={{
                transform: `translateY(${yOffset}px)`,
                transition: "transform 0.1s ease-out",
                contain: "strict",
                perspective: "4000px",
                perspectiveOrigin: "100% 0",
                width: "100%",
                height: "100%",
                transformStyle: "preserve-3d",
                position: "relative",
              }}
            >
              {/* Transformed base */}
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                transition={{
                  delay: 0.5,
                  duration: 1,
                  ease: [0.22, 1, 0.36, 1],
                }}
                style={{
                  backgroundColor: "#09090B",
                  transformOrigin: "0 0",
                  backfaceVisibility: "hidden",
                  WebkitBackfaceVisibility: "hidden",
                  border: "1px solid #1e1e1e",
                  borderRadius: "10px",
                  width: "1600px",
                  height: "900px",
                  margin: "280px auto auto",
                  position: "absolute",
                  top: 0,
                  bottom: 0,
                  left: 0,
                  right: 0,
                  transform: `translate(${baseTransform.translateX}%) scale(${baseTransform.scale}) rotateX(${baseTransform.rotateX}deg) rotateY(${baseTransform.rotateY}deg) rotate(${baseTransform.rotateZ}deg)`,
                  transformStyle: "preserve-3d",
                  overflow: "hidden",
                }}
              >
                <DashboardMockup />
              </motion.div>
            </div>
          </div>

          {/* Mobile Dashboard Preview */}
          <motion.div
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.6, delay: 0.3 }}
            className="md:hidden px-4 mt-10 mb-8"
          >
            <div className="bg-zinc-900/80 border border-zinc-800 rounded-2xl overflow-hidden">
              <div className="p-4 border-b border-zinc-800">
                <div className="flex items-center gap-2">
                  <div className="w-8 h-8 rounded-lg bg-emerald-500 flex items-center justify-center">
                    <span className="text-zinc-900 font-bold text-sm">P</span>
                  </div>
                  <span className="text-white font-medium">fluxpay Dashboard</span>
                </div>
              </div>
              <div className="p-4 space-y-3">
                <div className="bg-zinc-800/50 rounded-xl p-4">
                  <p className="text-zinc-500 text-xs mb-1">Saldo disponivel</p>
                  <p className="text-emerald-400 text-2xl font-bold">R$ 8.472,50</p>
                </div>
                <div className="grid grid-cols-2 gap-3">
                  <div className="bg-zinc-800/30 rounded-lg p-3">
                    <p className="text-zinc-500 text-xs mb-1">Entradas</p>
                    <p className="text-white font-semibold">R$ 15.840</p>
                    <p className="text-emerald-400 text-xs">+23%</p>
                  </div>
                  <div className="bg-zinc-800/30 rounded-lg p-3">
                    <p className="text-zinc-500 text-xs mb-1">Saidas</p>
                    <p className="text-white font-semibold">R$ 12.340</p>
                    <p className="text-zinc-400 text-xs">47 transf.</p>
                  </div>
                </div>
              </div>
            </div>
          </motion.div>

          <LogoCloud />
          <FeatureCardsSection />
          <AISection />
          <ProductDirectionSection />
          <WorkflowsSection />
          <DownloadAppSection />
          <CTASection />
          <Footer />
        </div>
      </section>
    </>
  )
}
